<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5.mb-5
    .titulo-principal.color-acento-contenido(data-aos="flip-up")
      .titulo-principal__numero
        span 4
      h1 Generalidades de microbiologa
    
    .row.justify-content-center
      .col-xl-5.col-lg-6.px-0.mb-lg-0.mb-4
        figure
          img(src="@/assets/curso/tema4/1.png" data-aos="zoom-in-right" alt="Imagen decorativa")
      .col-xl-5.col-lg-6.px-0
        .h-100.p-4.text-white.d-flex.flex-wrap.align-content-center(style="background-color:#7E5EE3;" data-aos="zoom-in-left")
          p La microbiologa es la ciencia que estudia los microorganismos como los virus, las bacterias, los hongos y los parsitos; todos son organismos microscpicos que hacen parte de la biologa de los seres vivos. A este grupo de microorganismos tambin se les denomina Microbios o Grmenes.
          p.mt-4.mb-0 Los microorganismos son clulas capaces de existir de forma libre o independiente, son los seres vivos ms diminutos que solo pueden ser observados a travs de un microscopio; la mayora son unicelulares, son de diferentes tamaos y formas, cada uno posee diversas caractersticas por lo que pueden ser patgenos, o sea, causantes de infecciones y enfermedad. 
    
    .col-xl-8.col-lg-10.m-auto.mt-5
      .cajon.color-acento-contenido.p-4(style="background-color:#BFEEF4;")
        p.mb-0(data-aos="zoom-in") El microorganismo patgeno o infeccioso, es cualquier germen capaz de producir en el ser humano (husped) infeccin o enfermedad. Tal capacidad es dada por la concentracin microbiana, la resistencia microbiana, el tamao del inculo y el ciclo en que se encuentre. 
    
    p.mt-5(data-aos="fade-right") La va de transmisin es el mecanismo mediante el cual el germen ingresa al husped susceptible (aquel que no ha desarrollado inmunidad activa o pasiva frente al germen infectante). Esta puede ser por:

    ul.fa-ul.mt-4.mb-0
      li.mb-2(data-aos="fade-right")
        i.fas.fa-atom.fa-li(style="color:#7E5EE3;")
        | Contacto.
      li.mb-2(data-aos="fade-right")
        i.fas.fa-atom.fa-li(style="color:#7E5EE3;")
        | Gotas.
      li.mb-2(data-aos="fade-right")
        i.fas.fa-atom.fa-li(style="color:#7E5EE3;")
        | Aerosoles.
      li.mb-2(data-aos="fade-right")
        i.fas.fa-atom.fa-li(style="color:#7E5EE3;")
        | Un vehculo comn.
      li(data-aos="fade-right")
        i.fas.fa-atom.fa-li(style="color:#7E5EE3;")
        | Vectores.
    
    Separador
    #t_4_1.titulo-segundo(data-aos="fade-right")
      h2 4.1 Bacterias, virus, hongos, protozoos
    
    p(data-aos="fade-right") A continuacin, se describen las caractersticas de los diferentes microorganismos mencionados anteriormente.

    h5.mt-5(data-aos="fade-left") Bacterias
    p(data-aos="fade-right") Organismos unicelulares procariotas, se reproducen sin invadir la clula (tuberculosis, clera, etc.) se caracterizan por:
    
    .tarjeta.tarjeta--azul.p-4.p-md-5.mt-4
      SlyderA(tipo="b")
        .row.justify-content-center
          .col-lg-5.order-lg-1.order-2
            p Las bacterias son organismos unicelulares, sin ncleo, diminutos que se alimentan del ambiente en que viven. En algunos casos, ese ambiente ser el cuerpo de otro ser vivo.
          .col-lg-7.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/tema4/2.png' alt="Muestra organismos unicelulares procariotas. Bacterias.")
        .row.justify-content-center
          .col-lg-5.order-lg-1.order-2
            p En el cuerpo humano, algunas bacterias ayudan a que el sistema digestivo funcione correctamente e impiden que entren bacterias nocivas en su interior; por el contrario, las bacterias tambin pueden causar problemas, como las caries dentales, las infecciones de las vas urinarias, las infecciones de odo o la faringitis.
          .col-lg-7.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/tema4/3.png' alt="Imagen decorativa")
        .row.justify-content-center
          .col-lg-5.order-lg-1.order-2
            p Otras bacterias se utilizan para elaborar medicamentos y vacunas. Para combatir una infeccin producida por bacterias, es necesario tomar o inyectar un antibitico, ste debe ser especfico para la clase de bacteria que est produciendo la infeccin, lo que significa que cualquier antibitico no sirve para una infeccin en el pulmn como en la piel.
          .col-lg-7.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/tema4/4.png' alt="Imagen decorativa")
        .row.justify-content-center
          .col-lg-5.order-lg-1.order-2
            p Las bacterias aerbicas obtienen energa por respiracin. Las anaerbicas por fermentacin (degradacin de glcidos en anaerobiosis). Otras utilizan el proceso de putrefaccin para su supervivencia. 
            p.mt-4 Ejemplos de bacterias: Escherichia coli y Salmonella, Helicobacter pylori, Neisseria gonorrhoeae, Staphylococcus aureus, Streptococcus, Pseudomonas, ente otras.
          .col-lg-7.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/tema4/5.png' alt="Imagen decorativa")
        .row.justify-content-center
          .col-lg-5.order-lg-1.order-2
            p Segn su forma, hay cuatro formas bsicas muy comunes en las bacterias: 
            ol.lista-ol--cuadro
              li 
                .lista-ol--cuadro__vineta
                  span.text-bold 1
                | Esfrica.
              li 
                .lista-ol--cuadro__vineta
                  span.text-bold 2
                | Alargada o cilndrica es un bacilo.
              li 
                .lista-ol--cuadro__vineta
                  span.text-bold 3
                |  Cuando aparecen uno o ms dobleces en la longitud de la clula que le dan forma espiral es un espirilo.
              li 
                .lista-ol--cuadro__vineta
                  span.text-bold 4
                | Si tiene forma de coma: vibrios.
          .col-lg-7.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/tema4/6.svg' alt="Imagen decorativa")
    
    h5.mt-5(data-aos="fade-left") Virus
    p(data-aos="fade-right") Parsitos celulares que se introducen en la clula para reproducirse (gripe, sida, dengue, etc.) algunas generalidades sobre los virus son:
    
    .tarjeta.tarjeta--azul.p-4.p-md-5.mt-4(data-aos="fade-right")
      SlyderA(tipo="b")
        .row.justify-content-center
          .col-lg-5.order-lg-1.order-2
            p Los virus no se consideran clulas completas, ms pequeos que las bacterias; solo son material gentico (DNA o RNA) empaquetado dentro de una cubierta proteica, por lo que necesitan usar las estructuras de otras clulas para poderse reproducir. 
          .col-lg-7.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/tema4/7.png' alt="Muestra un virus que son parsitos celulares que se introducen en la clula.")
        .row.justify-content-center
          .col-lg-5.order-lg-1.order-2
            p Los virus no sobreviven sin estar dentro de un organismo vivo, como, por ejemplo:
            ol.lista-ol
              li 
                span.text-bold 
                | Una persona.
              li 
                span.text-bold  
                | Un animal.
              li 
                span.text-bold  
                | Una planta.
            p.mt-3 Fuera de clulas vivas, los virus pueden vivir por muy poco tiempo, por esta razn, los virus que se encuentran en fluidos corporales como saliva, moco, esputo, sudor, al quedar en las superficies, como la chapa o manija de una puerta o el asiento de un inodoro, solo pueden vivir durante un perodo breve de tiempo; morirn rpidamente a menos que entren en contacto con las mucosas de un nuevo organismo vivo. Vale la pena aclarar, que la capacidad que tienen algunos virus de mutar o cambiar rpidamente, ha hecho que se vuelvan ms fuertes fuera del husped y por lo tanto mucho mas agresivos o peligrosos, especialmente para el ser humano.
          .col-lg-7.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/tema4/8.svg' alt="Imagen decorativa")
        .row.justify-content-center
          .col-lg-5.order-lg-1.order-2
            p Cuando un virus se introduce en el cuerpo de una persona, se multiplican rpidamente hasta hacerla enfermar. Hay virus que originan enfermedades de poca importancia, como la gripe o el resfriado comn; tambin pueden originar enfermedades muy graves, como el SIDA, SARS-CoV 2 y el bola, entre otros. Para combatir los virus se utilizan:
            ol.lista-ol
              li 
                span.text-bold 
                | Los medicamentos antivirales.
              li 
                span.text-bold  
                | Los medicamentos retrovirales.
            p.mt-3 Algunas vacunas sirven para prevenir o mejorar la respuesta inmunolgica del organismo.
            p.mt-4 Por lo anterior es importante resaltar que los antibiticos no son eficaces contra los virus.
          .col-lg-7.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/tema4/9.svg' alt="Imagen decorativa")
        .row.justify-content-center
          .col-lg-5.order-lg-1.order-2
            p El sistema inmunolgico del ser humano utiliza diversas formas para atacar los agentes infecciosos; los virus tratan de evitarlo, haciendo ms copias de s mismo, para de esta forma propagarse a otros huspedes. Las caractersticas o adaptaciones nuevas que hacen evadir las defensas del organismo, es lo que se llama mutacin, lo cual se mantiene de una generacin a otra del virus, mientras que las defensas de los huspedes tienden a perderse.
          .col-lg-7.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/tema4/10.png' alt="Imagen decorativa")
        .row.justify-content-center
          .col-lg-5.order-lg-1.order-2
            p El modo en que los huspedes se defienden de un virus es desarrollando anticuerpos, los cuales se fijan a las protenas de la superficie exterior del virus, con lo que le impiden entrar a las clulas del husped. 
            p.mt-4 Un virus que simula ser diferente a otros que han infectado al husped tiene una ventaja, ya que el husped no tiene una inmunidad precedente contra ese virus en forma de anticuerpos. 
          .col-lg-7.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/tema4/11.png' alt="Imagen decorativa")
        .row.justify-content-center
          .col-lg-5.order-lg-1.order-2
            p Muchas adaptaciones virales involucran cambios en la superficie exterior del virus. Un ejemplo tpico es el virus que origina la influenza; cuando se inician los sntomas el organismo inicia un proceso de defensas contra ese virus especfico, la infeccin se desarrolla y termina, creando en ese individuo una memoria inmunolgica para defenderse posteriormente; sin embargo esa misma persona  con el tiempo se expone o tiene contacto nuevamente con otra persona con el virus de la influenza puede volver a enfermar ya que es una nueva cepa del mismo virus, para lo cual su organismo no est preparado y no se defiende. 
            p.mt-4 Por lo anterior es que para los adultos mayores y los nios es muy importante vacunarse cada ao contra la influenza, la cual contiene la informacin de varias familias de virus y el sistema inmunolgico se prepara para defenderse.
          .col-lg-7.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/tema4/12.png' alt="Imagen decorativa")
    
    h5.mt-5(data-aos="fade-left") Se invita al aprendiz a ver el vdeo explicativo Cmo mutan los virus y cmo podra evolucionar el coronavirus, ubicado en el material complementario.
    
    h5.mt-5(data-aos="fade-left") Hongos
    p(data-aos="fade-right") Organismos unicelulares microscpicos o multicelulares macroscpicos; aerobios y hetertrofos, parecidos a las plantas, se caracterizan por:
    
    .tarjeta.tarjeta--azul.p-4.p-md-5.mt-4(data-aos="fade-right")
      SlyderA(tipo="b")
        .row.justify-content-center
          .col-lg-5.order-lg-1.order-2
            p Consiguen el alimento de las plantas, de los alimentos y de los animales en ambientes hmedos y clidos. Son organismos eucariotas, incapaces de sintetizar sus nutrientes y por lo tanto se consideran saprfitos o parsitos, descomponen la materia orgnica, y son capaces de contaminar los cuerpos de otros seres vivos. Poseen pared celular como las plantas, pero de quitina en lugar de celulosa, y se reproducen mediante esporas, tanto sexual o asexualmente. 
          .col-lg-7.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/tema4/13.png' alt="Muestra una imagen de hongos en una fruta.")
        .row.justify-content-center
          .col-lg-5.order-lg-1.order-2
            p Las infecciones por hongos, como el pie de atleta (Tinea pedis), que se aloja en la piel, por la humedad, producto de la sudoracin, no representan ningn peligro para una persona sana. Sin embargo, las personas inmunodeprimidas, por enfermedades como el SIDA o el cncer, pueden desarrollar infecciones por hongos mucho ms graves.
          .col-lg-7.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/tema4/14.png' alt="Imagen decorativa")
        .row.justify-content-center
          .col-lg-5.order-lg-1.order-2
            p Las infecciones por levaduras, que se originan cuando se pierde el equilibro de los microorganismos que habitualmente conviven en el cuerpo no suelen ser peligrosas, sin embargo, al ingresar al torrente sanguneo se pueden propagar a otras partes del cuerpo; este tipo de infecciones son muy comunes en recin nacidos de bajo peso, personas diabticas, o pacientes en tratamiento oncolgico.
          .col-lg-7.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/tema4/15.svg' alt="Imagen decorativa")
        .row.justify-content-center
          .col-lg-5.order-lg-1.order-2
            p Las infecciones por hongos son llamadas micticas y son tratables en su gran mayora por medicamentos conocidos como fungicidas o antimicticos; pueden ser transmisibles, como por ejemplo la Candida albicans (origen de la candidiasis de transmisin sexual). 
          .col-lg-7.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/tema4/16.png' alt="Imagen decorativa")
    
    h5.mt-5(data-aos="fade-left") Protozoos
    p(data-aos="fade-right") Organismos unicelulares, como las bacterias, se hallan en ambientes hmedos o acuticos, y se pueden considerar animales microscpicos. 
    
    .tarjeta.tarjeta--azul.p-4.p-md-5.mt-4(data-aos="zoom-in")
      SlyderA(tipo="b")
        .row.justify-content-center
          .col-lg-5.order-lg-1.order-2
            p Se les llama de 2 maneras, protozoos o protozoarios, sin embargo, hacen parte de un reino propio llamado protozoa; y forman parte del Reino protista, puesto que se consideran el primer eslabn evolutivo de las clulas eucariotas, es decir antes de los animales, plantas, hongos y algas. Ostentan movilidad propia, se transportan por medio de flagelos, cilios o del alargamiento de su citoplasma como falsos pies.
          .col-lg-7.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/tema4/17.svg' alt="Muestra la imagen de protozoos.")
        .row.justify-content-center
          .col-lg-5.order-lg-1.order-2
            p A los protozoarios, les encanta la humedad, por lo cual, las infecciones intestinales y otras enfermedades gastrointestinales, como las amebas y las giardias, fcilmente se transmiten a travs del agua contaminada. Algunos protozoos son parsitos lo que significa que para vivir necesitan estar dentro de otro ser vivo. Por ejemplo, el protozoo que causa la malaria crece dentro de los eritrocitos, los cuales destruye cada vez que se reproducen. Otro ejemplo es la ameba, la cual se encapsula formando quistes, para poder vivir fuera del cuerpo humano y en ambientes extremos durante mucho tiempo; sin que esto sea un problema para su supervivencia.
          .col-lg-7.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/tema4/18.svg' alt="Imagen decorativa")
        .row.justify-content-center
          p Los protozoos se clasifican en:
          .ps-3.pe-2.mt-2
            .p-1(style="max-width:200px;background-color:#FF9D00;border:3px white solid;border-radius:4px;")
              h5.mb-0.ps-2.text-center Rizpodos
            p.mt-2 Desplazamiento mediante pseudpodos (falsos pies), que al mismo tiempo les sirven para atrapar el alimento slido e introducirlo al citoplasma lo que se llama: fagocitosis.
            .p-1.mt-4(style="max-width:200px;background-color:#00BCD4;border:3px white solid;border-radius:4px;")
              h5.mb-0.ps-2.text-center Flagelados
            p.mt-2 Clulas dotadas de uno o ms flagelos, llamadas colas con las cuales se impulsan hacia adelante en el medio ambiente en que se encuentren.
            .p-1.mt-4(style="max-width:200px;background-color:#8CBCFA;border:3px white solid;border-radius:4px;")
              h5.mb-0.ps-2.text-center Ciliados
            p.mt-2 La membrana plasmtica se encuentra rodeada de cilios, especie de filamentos ms pequeos y numerosos que los flagelos, que tambin sirven para movilizarse.
            .p-1.mt-4(style="max-width:200px;background-color:#5B33DB;border:3px white solid;border-radius:4px;")
              h5.mb-0.ps-2.text-white.text-center Esporozoos
            p.mt-2 Son los famosos parsitos, con poca movilidad, pero poseen un tipo de reproduccin asexual que consiste en producir esporas, que generan un nuevo individuo idntico, los cuales son muy resistentes. Esta forma de divisin mltiple es conocida como esporulacin.
          p.mt-1 Ejemplos de protozoos: Paramecio, Giardia. Amoeba. Tricomona; tambin son protozoos el origen de infecciones como la Malaria, Amebiasis y toxoplasmosis. 

    h5.mt-5(data-aos="fade-left") Priones
    p(data-aos="fade-right") Prion de la encefalopata espongiforme (enfermedad de la vaca loca).

    .col-xl-8.m-auto.mt-4.col-lg-10
      figure
        img(src="@/assets/curso/tema4/19.png" data-aos="zoom-in-up" alt="Muestra la imagen de protozoos.")
    
    Separador
    #t_4_2.titulo-segundo(data-aos="fade-right")
      h2 4.2 Flora humana
    
    p(data-aos="fade-right") La piel y las mucosas del cuerpo humano representa una gran superficie de contacto con el medio ambiente; en ella residen microorganismos con caractersticas distintas de humedad, temperatura, pH y disponibilidad de nutrientes.
    p.mt-4(data-aos="fade-right") Los microorganismos que viven con el husped en su estado normal, sin causarle enfermedad, es lo que se llama la flora humana; sin embargo, si aumentan en cantidad o llegan nuevas especies pueden dar origen a infecciones o enfermedades; es ah donde representa un importante mecanismo de defensa del husped. Contribuye al desarrollo de la respuesta inmunolgica, adems ayuda a evitar la colonizacin de la piel o las mucosas por bacterias que pueden ser patgenas.
    
    .col-xl-7.col-lg-10.m-auto.mt-5(data-aos="fade-right")
      .cajon.color-acento-contenido.p-4(style="background-color:#BFEEF4;")
        p.mb-0 La flora normal son microorganismos (bacterias y levaduras) que colonizan las mucosas y la piel, sin embargo, en el cuerpo humano existen partes que son estriles en condiciones normales. 
    
    h5.mt-5(data-aos="fade-left") Flora basal o residente
    p(data-aos="fade-right") Est constituida por grmenes que siempre estn presentes, sin mayor potencial de enfermar. Por ejemplo: Staphylococcus epidermidis en la piel o E. coli en el intestino.

    .col-xl-10.m-auto.mt-5
      .row
        .col-lg-6.mb-4.mb-lg-0
          figure
            img(src="@/assets/curso/tema4/20.png" data-aos="zoom-in-right" alt="Imagen que representa la flora residente y transitoria de la piel, la cual contiene los siguientes textos con cada germen: Mi nombre es Bacillus SP soy un contaminante normal. Me llamo Streptococcus beta grupo a me gusta la garganta. Causo diarreas mi nombre es Shigella. Yo me llamo Influenza A y amo la neumona. Yo soy Klebsiella puendo infectar heridas. Yo soy Clostridium difficile y causo colitis. Hola! Soy Starhylococcus puedo causarte infecciones graves. Llmame Bacteroide te gustara una infeccin de odo o una vaginitis? Me llamo Haemophilus soy altamente contagioso en conjuntivitis. Amo a la gente! Causo infecciones urinarias. Me llamo Proteus. Soy un arma llamada Pseudomona aeruginosa yo infecto heridas con pus azul-verdoso. Bu! Yo soy Escherichia coli. Causo diarreas e infecciones del tracto urinario.")
        .col-lg-6
          figure
            img(src="@/assets/curso/tema4/21.png" data-aos="zoom-in-left" alt="Imagen que representa la flora residente y transitoria de la piel, la cual contiene los siguientes textos con cada germen: Mi nombre es Bacillus SP soy un contaminante normal. Me llamo Streptococcus beta grupo a me gusta la garganta. Causo diarreas mi nombre es Shigella. Yo me llamo Influenza A y amo la neumona. Yo soy Klebsiella puendo infectar heridas. Yo soy Clostridium difficile y causo colitis. Hola! Soy Starhylococcus puedo causarte infecciones graves. Llmame Bacteroide te gustara una infeccin de odo o una vaginitis? Me llamo Haemophilus soy altamente contagioso en conjuntivitis. Amo a la gente! Causo infecciones urinarias. Me llamo Proteus. Soy un arma llamada Pseudomona aeruginosa yo infecto heridas con pus azul-verdoso. Bu! Yo soy Escherichia coli. Causo diarreas e infecciones del tracto urinario.")
      figcaption.fw-normal.mt-3 https://www.passeidireto.com/arquivo/50712409/higienizacao-das-maos

    p.mt-5(data-aos="fade-up") Algunas generalidades de la flora humana son: 

