              li.mb-3
                i.fas.fa-atom.fa-li(style="color:#7E5EE3;")
                p.mb-0 #[strong Efectos directos:] #[br] Produccin de bacteriocinas. #[br] Produccin de metabolitos txicos. #[br] Reduccin del potencial redox. #[br] Consumo de nutrientes esenciales. #[br] Competencia por receptores.
              li
