      .py-3.py-md-4(titulo="La flora transitoria")
        .row.justify-content-center.px-2
          .col-lg-7.order-lg-1.order-2
            p Varia de un ser humano a otro y est compuesta por microorganismos que colonizan en forma intermitente una determinada rea, puede incluir bacterias potencialmente patgenas para el propio individuo u otras personas que entran en contacto con l.
          .col-lg-5.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/tema4/24.svg' alt="Imagen decorativa")
      .py-3.py-md-4(titulo="Equilibrio normal entre la flora <br> residente y transitoria")
        .row.justify-content-center.px-2
          .col-lg-7.order-lg-1.order-2
            p Cuando se pierde el equilibrio normal entre la flora residente y la flora transitoria, haciendo que aumente en cantidad la transitoria, es donde se presentan algunos signos como diarrea, fiebre, vmito, etc., que indican un mecanismo de defensa del organismo, por lo tanto, no se debe tratar de eliminar de inmediato el signo si no buscar el origen o causa, para iniciar el tratamiento adecuado. El abuso indiscriminado de los antibiticos hace que esa flora basal, reducida o dbil, cree resistencia a los tratamientos. La bacteria resistente contina creciendo y multiplicndose, haciendo a las infecciones ms difciles de tratar.
          .col-lg-5.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/tema4/25.svg' alt="Imagen decorativa")
    
    p.mt-5(data-aos="fade-right") A continuacin, se describe la flora normal en distintas partes del cuerpo humano.

    .titulo-sexto.color-acento-contenido.mt-5(data-aos="fade-right")
      h5 Tabla 3.
      span Flora normal del cuerpo humano

    .mt-4
      .tabla-a.color-acento-contenido(data-aos="zoom-in-up")
        table
          tbody
            tr(style="background-color:#F6F6F6;" data-aos="fade-right")
              td.p-4.fw-bold.text-center(style="border:0;border-right:1px #AFAFAF solid;width: 30%" ) Cavidad oral
              td.p-4(style="border:0;") La flora oral es de tipo mixto, grmenes aerobios y anaerobios. Las bacterias que se adhieren a la superficie dental, al aumentar por falta de higiene, producen enfermedades como caries y periodontitis.
            tr(style="background-color:#FFF;" data-aos="fade-right")
              td.p-4.fw-bold.text-center(style="border:0;border-right:1px #AFAFAF solid;width: 30%" ) Refrigeracin
              td.p-4(style="border:0;") El tubo digestivo alberga muchas bacterias responsables de la sntesis de Vit K y Vit del complejo B. Adems, compite con los microorganismos patgenos por nutrientes y receptores y elabora bacteriocinas.
            tr(style="background-color:#F6F6F6;" data-aos="fade-right")
              td.p-4.fw-bold.text-center(style="border:0;border-right:1px #AFAFAF solid;width: 30%" ) Vaginal
              td.p-4(style="border:0;") 
                p Depende de la cantidad de estrgenos, ya que estos determinan el nmero de clulas epiteliales, aumentando el glucgeno y disminuyendo el pH. La acidez inhibe las bacterias. Funciones: protege de la infeccin vaginal, en especial durante el embarazo.
