            tr(style="background-color:#FFF;" data-aos="fade-right")
              td.p-4.fw-bold.text-center(style="border:0;border-right:1px #AFAFAF solid;width: 30%" ) Aparato respiratorio
              td.p-4(style="border:0;") El aparato respiratorio est dividido en dos: alto y bajo. En el sujeto normal solamente el rbol respiratorio alto (fosas nasales y faringe) presenta flora normal: los senos paranasales, odo medio, trquea, bronquios pulmonares y pleura son estriles.
