              td.p-4(style="border:0;") Salvo la uretra anterior, el aparato urinario es estril. La orina contribuye a mantener la va urinaria libre de grmenes, debido al arrastre, al pH cido y a su elevada osmolaridad. 
    
    Separador
    #t_4_3.titulo-segundo(data-aos="fade-right")
      h2 4.3 Cadena de transmisin
    
    p(data-aos="fade-right") La ruta o serie de eslabones del agente infeccioso desde la fuente y los elementos que se articulan en la transmisin del germen hasta ocasionar una infeccin en el husped vulnerable, es lo que se llama cadena de transmisin o epidemiolgica.

    .col-xl-7.col-lg-10.m-auto.mt-5
      .cajon.color-acento-contenido.p-4(style="background-color:#BFEEF4;")
        p.mb-0(data-aos="zoom-in") Una infeccin es un estado de enfermedad resultante de la invasin y el crecimiento de microbios en el cuerpo. El equipo de salud debe seguir ciertas prcticas que previenen la propagacin de la infeccin. El objetivo es proteger de las infecciones a pacientes, visitantes y al propio personal.
    
    p.mt-5(data-aos="fade-right") Los actores que participan en la cadena de transmisin son:

    ImagenInfografica.color-acento-botones.mt-5(data-aos="zoom-in")
      template(v-slot:imagen)
        figure
          img(src='@/assets/curso/tema4/26.svg' alt="Imagen decorativa")
      .tarjeta.color-acento-botones.bg-white.p-3(x="5%" y="33%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
        p.mb-0(style="color:inherit;") Microorganismo (virus, bacteria, hongo, rickettsia, protozoario o parsitos) que invaden el cuerpo de un ser vivo ocasionando daos en sus tejidos (sntomas). Germen capaz de producir una infeccin o enfermedad infecciosa. 
        p.mt-3(style="color:inherit;") Propiedades: transmisibilidad, patogenicidad e inmunogenicidad.  
      .tarjeta.color-acento-botones.bg-white.p-3(x="17%" y="33%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
        p.mb-0(style="color:inherit;") Camino que toma el agente para salir de la fuente: Aparato respiratorio, Aparato digestivo, Piel, Placenta, Tracto genitourinario, etc.
      .tarjeta.color-acento-botones.bg-white.p-3(x="29%" y="33%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
        p.mb-0(style="color:inherit;") Forma en que el agente infeccioso se transmite del reservorio al husped; se propaga de una fuente o reservorio a una persona. Puede ser transmisin directa e indirecta.
      .tarjeta.color-acento-botones.bg-white.p-3(x="23.5%" y="50%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
        p.mb-0(style="color:inherit;")  Transferencia directa del agente infeccioso a una puerta de entrada para que se pueda llevar a cabo la infeccin. Transmisin de persona a persona, a travs de rociado de gotillas por las conjuntivas o membranas mucosas de la nariz o boca al estornudar, toser, escupir, hablar o cantar, y por contacto directo como al tocar, besar, al tener relaciones sexuales.
      .tarjeta.color-acento-botones.bg-white.p-3(x="26%" y="66%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
        p.mb-0(style="color:inherit;") Objetos o materiales contaminados como juguetes, pauelos, instrumentos quirrgicos, agua, alimentos, leche, productos biolgicos, incluyendo suero y plasma. El agente puede o no haberse multiplicado o desarrollado en el vehculo antes de ser transmitido.
      .tarjeta.color-acento-botones.bg-white.p-3(x="35.5%" y="66%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
        p.mb-0(style="color:inherit;") Insecto o cualquier portador vivo que transporta un agente infeccioso desde un individuo o sus desechos, hasta un individuo susceptible, su comida o su ambiente inmediato. El agente puede o no desarrollarse, propagarse o multiplicarse dentro del vector.
      .tarjeta.color-acento-botones.bg-white.p-3(x="45%" y="66%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
        p.mb-0(style="color:inherit;") Diseminacin de aerosoles microbianos transportados hacia una puerta de entrada apropiada, generalmente el tracto.
      .tarjeta.color-acento-botones.bg-white.p-3(x="38%" y="78%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
        p.mb-0(style="color:inherit;") El agente necesariamente debe propagarse (multiplicarse), desarrollarse cclicamente o ambos (ciclo propagacin) en el artrpodo vector antes que pueda transmitir la forma infectante al ser humano.
      .tarjeta.color-acento-botones.bg-white.p-3(x="38%" y="89%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
        p.mb-0(style="color:inherit;") Es el simple traslado mecnico del agente infeccioso por medio de un insecto terrestre o volador, ya sea por contaminacin de sus patas o trompa o por el pase a travs de su tracto gastrointestinal, sin multiplicacin o desarrollo cclico del microorganismo.
      .tarjeta.color-acento-botones.bg-white.p-3(x="58%" y="33%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
        p.mb-0(style="color:inherit;") Individuo (o animal) infectado, que alberga un agente infeccioso especfico de una enfermedad, sin presentar sntomas o signos clnicos, constituye fuente potencial de infeccin para el ser humano. Para que se origine una infeccin especfica; el individuo debe cumplir con una serie de aspectos estructurales y funcionales.
      .tarjeta.color-acento-botones.bg-white.p-3(x="55%" y="45%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
        p.mb-0(style="color:inherit;") Es el intervalo de tiempo que transcurre entre la exposicin a un agente infeccioso y la aparicin del primer signo o sntoma de la enfermedad.
      .tarjeta.color-acento-botones.bg-white.p-3(x="55%" y="57.5%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
        p.mb-0(style="color:inherit;") Es el intervalo de tiempo que transcurre desde que se produce la infeccin hasta que la persona se vuelve infecciosa.
      .tarjeta.color-acento-botones.bg-white.p-3(x="55%" y="67%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
        p.mb-0(style="color:inherit;") Es el intervalo de tiempo durante el cual el agente infeccioso puede ser transferido directa o indirectamente de una persona infectada a otra persona, de un animal infectado al ser humano o de un ser humano infectado a un animal, inclusive artrpodos. Como regla general la mayora de las enfermedades no son transmisibles durante la fase inicial del periodo de incubacin ni despus del completo restablecimiento del enfermo.
      .tarjeta.color-acento-botones.bg-white.p-3(x="74%" y="33%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
        p.mb-0(style="color:inherit;") Camino por el cual un agente infeccioso sale de su husped o puerta de salida. 
      .tarjeta.color-acento-botones.bg-white.p-3(x="74%" y="49%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
        p.mb-0(style="color:inherit;") Las enfermedades que utilizan esta puerta de salida son las de mayor difusin y las ms difciles de controlar (tuberculosis, influenza, sarampin, etc.) 
      .tarjeta.color-acento-botones.bg-white.p-3(x="74%" y="62%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
        p.mb-0(style="color:inherit;") Propias de la sfilis, SIDA, gonorrea otras enfermedades de transmisin sexual, leptospirosis. 
      .tarjeta.color-acento-botones.bg-white.p-3(x="74%" y="74%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
        p.mb-0(style="color:inherit;") Propias de la tifoidea, hepatitis A y E, clera, amebiasis. 
      .tarjeta.color-acento-botones.bg-white.p-3(x="74.2%" y="86.5%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
        p.mb-0(style="color:inherit;") A travs de contacto directo con lesiones superficiales, como en la varicela, herpes zoster y sfilis. Por picaduras, mordeduras, perforacin por aguja u otro mecanismo que conlleve contacto con sangre infectada, como en la sfilis, enfermedad de Chagas, malaria, leishmaniasis, fiebre amarilla, hepatitis B, etc. 
      .tarjeta.color-acento-botones.bg-white.p-3(x="74.2%" y="99%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
        p.mb-0(style="color:inherit;") En general la placenta es una barrera efectiva de proteccin del feto contra infecciones de la madre; sin embargo, no es totalmente efectiva para algunos agentes infecciosos como los de la sfilis, rubola, toxoplasmosis, SIDA y enfermedad de Chagas.
      .tarjeta.color-acento-botones.bg-white.p-3(x="90.5%" y="33%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
        p.mb-0(style="color:inherit;") Organismo (animal, vegetal o humano) donde vive y se multiplica un agente infeccioso. 
      .tarjeta.color-acento-botones.bg-white.p-3(x="86%" y="49%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
        p.mb-0(style="color:inherit;") El hbitat normal en que vive se multiplica y/o crece un agente infeccioso.
      .tarjeta.color-acento-botones.bg-white.p-3(x="96%" y="49%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
        p.mb-0(style="color:inherit;")  Constituye una fuente, pero no un reservorio porque en l no puede multiplicarse el agente.
      .tarjeta.color-acento-botones.bg-white.p-3(x="85%" y="55.8%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
        p.mb-0(style="color:inherit;") Es cualquier ser humano, animal, artrpodo, planta, suelo o materia inanimada, donde normalmente vive y se multiplica un agente infeccioso y del cual depende para su supervivencia, reproducindose de manera que pueda ser transmitido a un husped susceptible.
      .tarjeta.color-acento-botones.bg-white.p-3(x="94%" y="64.5%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
        p.mb-0(style="color:inherit;")  El hecho de que una enfermedad o grupo de enfermedades tenga al ser humano como reservorio es de gran importancia prctica, ya que las medidas de control que se adoptan pueden circunscribirse al mismo ser humano. Por ejemplo, si una enfermedad se puede tratar con un antibitico adecuado, la accin directa se ejerce sobre el sujeto como paciente y como reservorio. 
        p.mt-3(style="color:inherit;") El reservorio principal de enfermedades como las de transmisin sexual, lepra, tos ferina, sarampin y fiebre tifoidea es el ser humano.
      .tarjeta.color-acento-botones.bg-white.p-3(x="97%" y="73.5%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
        p.mb-0(style="color:inherit;") Los animales pueden ser infectados y a la vez servir como reservorio para varias enfermedades del ser humano. Son ejemplos de ello la brucelosis, la leptospirosis, la peste, la psitacosis, la rabia y el ttanos.
    
    p.mt-5(data-aos="fade-right") Y los mtodos de transmisin son:

