    Separador
    #t_4_4.titulo-segundo(data-aos="fade-right")
      h2 4.4 Bioseguridad
    
    p(data-aos="fade-right") Es el conjunto de principios, normas, protocolos, tecnologas y prcticas que se realizan como medidas preventivas, con el fin de mantener el control de factores de riesgo laborales originarios de agentes biolgicos, fsicos o qumicos, al realizar procedimientos que pueden afectar la salud y la seguridad de los trabajadores de la salud, pacientes, cuidadores, visitantes y del medio ambiente. 
    p.mt-4(data-aos="fade-right") Se debe evitar el contacto y/o exposicin de la piel (intacta o lesionada) o mucosas, con los fluidos corporales, teniendo como premisa que todos los pacientes son considerados potencialmente infecciosos, sin importar el diagnstico o la afinidad con el individuo. Por lo anterior, se debe fomentar el uso correcto de los elementos de proteccin personal (E.P.P), el lavado de manos antes y despus del contacto con reas, equipos, pacientes, talento humano en salud, cuidadores o familiares, higienizacin o desinfeccin de manos.
    p.mt-4(data-aos="fade-right") Los fluidos del cuerpo son:

    ul.fa-ul.mt-4.mb-0
      li.mb-2(data-aos="fade-right")
        i.fas.fa-atom.fa-li(style="color:#7E5EE3;")
        | Sangre.
      li.mb-2(data-aos="fade-right")
        i.fas.fa-atom.fa-li(style="color:#7E5EE3;")
        | Semen.
      li.mb-2(data-aos="fade-right")
        i.fas.fa-atom.fa-li(style="color:#7E5EE3;")
        | Secrecin vaginal. 
      li.mb-2(data-aos="fade-right")
        i.fas.fa-atom.fa-li(style="color:#7E5EE3;")
        | Leche materna.
      li.mb-2(data-aos="fade-right")
        i.fas.fa-atom.fa-li(style="color:#7E5EE3;")
        | Lquido cefalorraqudeo. 
      li.mb-2(data-aos="fade-right")
        i.fas.fa-atom.fa-li(style="color:#7E5EE3;")
