        .bloque-texto-g__texto.p-4(data-aos="fade-left")
          p.mb-0  Los guantes desechables, de ltex o nitrilo, son para un solo uso, para evitar cualquier tipo de contaminacin cruzada. Primero que todo, debe seleccionar la talla que corresponde a la mano. Es conveniente que el guante quede ajustado para evitar arrugas y aumentar la sensibilidad y el tacto.
          br
