    #t_4_5.titulo-segundo(data-aos="fade-right")
      h2 4.5 Tcnica asptica
    
    p(data-aos="fade-right") Es la tcnica que se utiliza para eliminar la presencia de microorganismos patgenos en un rea u objeto determinado, siendo los siguientes, los trminos importantes para entender la tcnica:

    .col-xl-11.m-auto.mt-5
      ImagenInfografica.infografia5.color-acento-contenido(data-aos="zoom-in")
        template(v-slot:imagen)
          figure
            img(src='@/assets/curso/tema4/47.svg' alt="Imagen decorativa")
        .tarjeta.color-acento-botones.bg-white.p-3(x="12%" y="20%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
          p.mb-0(style="color:inherit;") Libre de microorganismos.
        .tarjeta.color-acento-botones.bg-white.p-3(x="37%" y="20%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
          p.mb-0(style="color:inherit;") Remover con agua y jabn la mugre o suciedad de un objeto o superficie.
        .tarjeta.color-acento-botones.bg-white.p-3(x="63%" y="20%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
          p.mb-0(style="color:inherit;") Son las medidas empleadas para prevenir o combatir la infeccin destruyndola o inhibiendo las bacterias por medio de agentes fsicos o qumicos. No destruye esporas.
        .tarjeta.color-acento-botones.bg-white.p-3(x="88%" y="20%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
          p.mb-0(style="color:inherit;") Aplicacin de sustancias qumicas para inhibir o reducir el nmero de microorganismos de la piel viva, mucosas o heridas, de tal forma que no se inicien procesos infecciosos. 
        .tarjeta.color-acento-botones.bg-white.p-3(x="12%" y="46%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
          p.mb-0(style="color:inherit;") Sustancia qumica de aplicacin tpica sobre tejidos vivos (piel intacta, mucosas, heridas, entre otros), que destruye o inhibe los microorganismos sin afectar sensiblemente a los tejidos donde se aplica.
        .tarjeta.color-acento-botones.bg-white.p-3(x="37%" y="46%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
          p.mb-0(style="color:inherit;") Objeto o elemento libre de todo microorganismo (patgeno y no patgeno) incluyendo esporas.
        .tarjeta.color-acento-botones.bg-white.p-3(x="63%" y="46%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
          p.mb-0(style="color:inherit;") Cuando un objeto estril se pone en contacto con otro sucio o infectado por microorganismos.
        .tarjeta.color-acento-botones.bg-white.p-3(x="88%" y="46%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
          p.mb-0(style="color:inherit;") Superficies o rea donde se trabaja con elementos limpios o estriles. 
        .tarjeta.color-acento-botones.bg-white.p-3(x="12%" y="72%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
          p.mb-0(style="color:inherit;") Superficies o rea donde se desechan fluidos corporales, depsito y lugar para lavar y descontaminar elementos utilizados con los pacientes, sitio donde se almacena los compreseros con la ropa sucia de los pacientes.
        .tarjeta.color-acento-botones.bg-white.p-3(x="37%" y="72%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
          p.mb-0(style="color:inherit;") Agente qumico que impide la reproduccin de bacterias. 
        .tarjeta.color-acento-botones.bg-white.p-3(x="63%" y="72%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
          p.mb-0(style="color:inherit;") Sustancia que destruye bacterias.
        .tarjeta.color-acento-botones.bg-white.p-3(x="88%" y="72%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
          p.mb-0(style="color:inherit;") Sustancia tenso activo diseada para remover y eliminar la contaminacin indeseada de alguna superficie. Los detergentes de uso domstico, no usar en instrumental mdico. 
        .tarjeta.color-acento-botones.bg-white.p-3(x="38%" y="98%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
          p.mb-0(style="color:inherit;") Sustancia en la que se combina un detergente y un desinfectante qumico. No todos los detergentes y desinfectantes son compatibles. 
        .tarjeta.color-acento-botones.bg-white.p-3(x="63%" y="98%" numero="+" style="box-shadow:0px 0px 20px rgba(0,0,0,0.15);")
          p.mb-0(style="color:inherit;") Son detergentes que contienen enzimas proteolticas que disuelven la materia orgnica y estn especialmente diseados para el lavado de instrumental y equipo mdico.
    
    h5.mt-5(data-aos="fade-left") Compuestos utilizados en limpieza, desinfeccin y antisepsia
    p.mt-4(data-aos="fade-right") En el entorno hospitalario y donde se atiende pacientes, como consultorios, drogueras, farmacias, la desinfeccin se realiza utilizando diferentes agentes qumicos; la desinfeccin se clasifica de acuerdo con el nivel o al espectro de actividad del agente:

    br
    .row.mt-5
      .col-lg.mb-5
        .p-4.h-100(style="border:5px #8CBCFA solid;border-radius:20px;position:relative;box-shadow:0px 0px 20px rgba(0,0,0,.12);")
          .text-center(style="position:absolute;width:100%;top:-30px;left:0;") 
            span.text-white.fw-bold(data-aos="zoom-in-down" style="background-color:#8CBCFA;border:9px #fff solid;border-radius:100%;font-size:40px;padding:.4rem 1.2rem;box-shadow:0px 0px 20px rgba(0,0,0,.1);") 1
          h5.mb-0.text-center.mt-5.mb-5(data-aos="zoom-in") Desinfeccin de alto nivel
          p.mt-4.text-center(data-aos="zoom-in")  Eliminan todos los microorganismos, excepto gran cantidad de esporas. Los liberadores de cloro en concentraciones muy altas pueden ser considerados de alto nivel, pero debido a sus efectos corrosivos, no son recomendados para inmersin de equipos mdicos. 
      .col-lg.mb-5
        .p-4.h-100(style="border:5px #00BCD4 solid;border-radius:20px;position:relative;box-shadow:0px 0px 20px rgba(0,0,0,.12);")
          .text-center(style="position:absolute;width:100%;top:-30px;left:0;") 
            span.text-white.fw-bold(data-aos="zoom-in-down" style="background-color:#00BCD4;border:9px #fff solid;border-radius:100%;font-size:40px;padding:.4rem 1.2rem;box-shadow:0px 0px 20px rgba(0,0,0,.1);") 2
          h5.mb-0.text-center.mt-5(data-aos="zoom-in") Desinfeccin de nivel intermedio
          p.mt-4.text-center(data-aos="zoom-in") Elimina mycobacterium, bacterias en estado vegetativo, mayora de virus y hongos, pero no necesariamente elimina esporas. 
      .col-lg.mb-5
        .p-4.h-100(style="border:5px #FF9D00 solid;border-radius:20px;position:relative;box-shadow:0px 0px 20px rgba(0,0,0,.12);")
          .text-center(style="position:absolute;width:100%;top:-30px;left:0;") 
            span.text-white.fw-bold(data-aos="zoom-in-down" style="background-color:#FF9D00;border:9px #fff solid;border-radius:100%;font-size:40px;padding:.4rem 1.2rem;box-shadow:0px 0px 20px rgba(0,0,0,.1);") 3
          h5.mb-0.text-center.mt-5(data-aos="zoom-in") Desinfeccin de bajo nivel
          p.mt-4.text-center(data-aos="zoom-in") Puede matar algunos hongos y algunos virus; no elimina esporas ni mycobacterium tuberculoso.
    
