    p.mt-5(data-aos="fade-right") El protocolo de limpieza y desinfeccin de las superficies y reas, est dictado por el siguiente procedimiento: 

    .tarjeta.tarjeta--gris.p-4.mt-5(data-aos="zoom-in")
      PasosB.color-secundario
        .row.col-lg-11.m-auto(titulo="Cronograma")
          .col-md-6.col-lg-4.mb-4.mb-md-0(style="place-self:center;")
            figure
              img(src='@/assets/curso/tema4/48.svg' alt="Imagen decorativa")
          .col-md-6.col-lg-8(style="place-self:center;")
            p Realizar un cronograma de limpieza para las distintas reas.
        .row.col-lg-11.m-auto(titulo="Equipos")
          .col-md-6.col-lg-4.mb-4.mb-md-0(style="place-self:center;")
            figure
              img(src='@/assets/curso/tema4/49.svg' alt="Imagen decorativa")
          .col-md-6.col-lg-8(style="place-self:center;")
            p Alistar equipos y materiales requeridos para el proceso de limpieza y desinfeccin.
        .row.col-lg-11.m-auto(titulo="EPP")
          .col-md-6.col-lg-4.mb-4.mb-md-0(style="place-self:center;")
            figure
              img(src='@/assets/curso/tema4/50.svg' alt="Imagen decorativa")
          .col-md-6.col-lg-8(style="place-self:center;")
            p Emplear Elementos de Proteccin Personal (EPP) durante todo el procedimiento.
        .row.col-lg-11.m-auto(titulo="Artculos de limpieza")
          .col-md-6.col-lg-4.mb-4.mb-md-0(style="place-self:center;")
            figure
              img(src='@/assets/curso/tema4/51.png' alt="Imagen decorativa")
          .col-md-6.col-lg-8(style="place-self:center;")
            p Preparar la cantidad necesaria de solucin de limpieza para el aseo diario, (segn cantidad de superficies y duracin o estabilidad de la preparacin). 
        .row.col-lg-11.m-auto(titulo="Limpieza en hmedo")
          .col-md-6.col-lg-4.mb-4.mb-md-0(style="place-self:center;")
            figure
              img(src='@/assets/curso/tema4/52.png' alt="Imagen decorativa")
          .col-md-6.col-lg-8(style="place-self:center;")
            p Emplear un pao hmedo para limpiar las paredes, los suelos y las otras superficies en vez de barrer con escoba o quitar el polvo en seco, para reducir que se propaguen los microorganismos y el polvo.
        .row.col-lg-11.m-auto(titulo="Direccionamiento limpieza")
          .col-md-6.col-lg-4.mb-4.mb-md-0(style="place-self:center;")
            figure
              img(src='@/assets/curso/tema4/53.svg' alt="Imagen decorativa")
          .col-md-6.col-lg-8(style="place-self:center;")
            p Lavar las superficies de arriba hacia abajo para que la suciedad caiga al suelo y sea lo ltimo de recoger.
        .row.col-lg-11.m-auto(titulo="Jerarqua limpieza")
          .col-md-6.col-lg-4.mb-4.mb-md-0(style="place-self:center;")
            figure
              img(src='@/assets/curso/tema4/54.svg' alt="Imagen decorativa")
          .col-md-6.col-lg-8(style="place-self:center;")
            p Limpiar primero las reas ms altas y de ah, siga limpiando hacia abajo - por ejemplo, se debe limpiar primero las lmparas del techo, despus las mesas, luego los estantes, y como ltimo, el piso. Utilice un pao de limpieza diferente para las superficies frecuentemente tocadas y por ende con mayor probabilidad de estar contaminadas.
        .row.col-lg-11.m-auto(titulo="Remocin mecnica")
          .col-md-6.col-lg-4.mb-4.mb-md-0(style="place-self:center;")
            figure
              img(src='@/assets/curso/tema4/55.png' alt="Imagen decorativa")
          .col-md-6.col-lg-8(style="place-self:center;")
            p Hacer la remocin mecnica entregando y friccionando las superficies.
        .row.col-lg-11.m-auto(titulo="Proceso limpieza")
